.navbar {
    background-color: #08152c;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menuBars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.navMenu {
    background-color: #08152c;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.navMenu.active {
    left: 0;
    transition: 350ms;
}

.navText {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
}

.navText a {
    text-decoration: none;
    color: gray;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.navText a:hover {
    background-color: #060b26;
}

.navMenuItems {
    width: 100%;
}

.navbarToggle {
    background-color: #08152c;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.login {
    text-decoration: none;
    color: gray;
    font-size: 18px;
}